define('admin-app/adapters/application', ['exports', 'ember-data'], function (exports, DS) {

	'use strict';

	exports['default'] = DS['default'].RESTAdapter.extend({
		namespace: 'api',
		headers: (function () {
			return {
				'Requested-With': 'ember',
				'X-Auth-Token': window.localStorage.getItem('makeit3d-apikey')
			};
		}).property().volatile()
	});

});