define('admin-app/helpers/ajax', ['exports', 'ember'], function (exports, Ember) {

	'use strict';

	exports['default'] = Ember['default'].Object.extend({
		namespace: (function () {
			return this.adapter.get('namespace');
		}).property('this.adapter'),
		headers: (function () {
			return this.adapter.get('headers');
		}).property().volatile(),
		getJSONwoDS: function getJSONwoDS(url) {
			var me = this;

			var promise = new Ember['default'].RSVP.Promise(function (resolve, reject) {
				Ember['default'].$.ajax({
					type: 'GET',
					url: me.get('namespace') + '/' + url,
					headers: {
						'X-Auth-Token': window.localStorage.getItem('makeit3d-apikey')
					}
				}).done(function (response) {
					resolve(response);
				}).fail(function (error) {
					reject(error);
				});
			});

			return promise;
		},
		getJSON: function getJSON(url) {
			var me = this;

			var promise = new Ember['default'].RSVP.Promise(function (resolve, reject) {
				Ember['default'].$.ajax({
					type: 'GET',
					url: me.get('namespace') + '/' + url,
					headers: me.get('headers')
				}).done(function (response) {
					resolve(response);
				}).fail(function (error) {
					reject(error);
				});
			});

			return promise;
		},
		post: function post(url, data) {
			var me = this;

			var promise = new Ember['default'].RSVP.Promise(function (resolve, reject) {
				Ember['default'].$.ajax({
					type: 'POST',
					url: me.get('namespace') + '/' + url,
					headers: me.get('headers'),
					data: data
				}).done(function (response) {
					resolve(response);
				}).fail(function (error) {
					reject(error);
				});
			});

			return promise;
		},
		'delete': function _delete(url) {
			var me = this;

			var promise = new Ember['default'].RSVP.Promise(function (resolve, reject) {
				Ember['default'].$.ajax({
					type: 'DELETE',
					url: me.get('namespace') + '/' + url,
					headers: me.get('headers')
				}).done(function (response) {
					resolve(response);
				}).fail(function (error) {
					reject(error);
				});
			});

			return promise;
		},
		put: function put(url, data) {
			var me = this;

			var promise = new Ember['default'].RSVP.Promise(function (resolve, reject) {
				Ember['default'].$.ajax({
					type: 'PUT',
					url: me.get('namespace') + '/' + url,
					headers: me.get('headers'),
					data: data
				}).done(function (response) {
					resolve(response);
				}).fail(function (error) {
					reject(error);
				});
			});

			return promise;
		}
	});

});